@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
body {
    color: white;
    font-family: 'Nunito', sans-serif;
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Home {
    background-color: #313847;
    text-align: center;
    min-height: 100vh;
    /* padding: 1rem 0; */
}

.Header {
    font-size: calc(25pt + (50 - 25) * ((100vw - 300px) / (1920 - 300)));
    background-color: #10131a;
    border-radius: 25px;
    min-height: calc(75px + (100 - 75) * ((100vw - 300px) / (1920 - 300)));
    text-align: center;
    line-height: calc(75px + (100 - 75) * ((100vw - 300px) / (1920 - 300)));
    border-style: solid;
    border-width: 1px;
    margin: 0 0 2rem 0
}
u {
    color: lightblue;
}

input {
    background-color: "white";
}
.FormContents {
    border-color: lightblue;
    background-color: #10131a;
    border-style: solid;
    border-width: 3px;
    border-radius: 10px;
    padding: 1rem;
    margin-bottom: 10rem;
}

::-webkit-input-placeholder {
    color: gray
}

:-ms-input-placeholder {
    color: gray
}

::-ms-input-placeholder {
    color: gray
}

::placeholder {
    color: gray
}
.Footer {
    width: 100%;
    position: fixed;
    bottom: 0px;
    margin-top: 4rem;
}

.FooterText {
    font-size: calc(10pt + (15 - 10) * ((100vw - 300px) / (1920 - 300)));
    min-height: 50px;
    width: 100%;
    background-color: #10131a;
    border-top-style: solid;
    border-top-width: 1px;
    line-height: 50px;
}

a {
    color: lightblue;
}
